const slugToTitleDictionary = {
    'react': 'React',
    'amelioration': 'Pack Amlioration',
    'confidentialites': 'Politique de confidentialit RGPD',
    'condition': 'Conditions gnrales de vente (CGV)',
    'decouverte': 'Pack Dcouverte',
    'mentions': 'Mentions lgales',
    'premium': 'Pack Premium',
    'remboursement': 'Politique de remboursement',
    'services': 'Nos Services',
    'contact': 'Contact',
    'propos': "Bienvenue  l'agence !",
    'realisations': 'Nos Ralisations',
    '': 'Accueil'
};

export const slugToTitle = (slug) => {
    return slugToTitleDictionary[slug] ||
    slug.replace(/-/g, ' ')
    .split(' ')
    .map(word => word.charAt(0).toUpperCase() + word.slice(1))
    .join(' ');
}